.f {
    display: flex; }
.bg-color {
    background-color: orange;
    background-image: radial-gradient(orange,red ); }
.full {
    width: 100%;
    height: 100%; }
.center {
    justify-content: center;
    align-items: center; }


//container haelt alles Top level
.container {
    display: flex;
    width: 100vw;
    height: 100vh;

    .background {
        position: relative;
        width: 40%;
        height: auto;
        margin: auto; }

    .lowlevelbg {
        position: absolute;
        align-items: center;
        color: white;
        .lautschrift {
            width: 80%;
            height: auto; } }

    .sozialnav {
        padding-right: 2%;
        justify-content: flex-end;
        align-items: center;
        position: absolute;

        & ul {
            display: flex;
            height: 30%;
            justify-content: space-between;
            align-items: center;
            flex-direction: column; }
        & img {
            height: 0.813rem; } } }
